    margin-bottom:15px;
    padding:10px 20px;
  }
}
.table-responsive{
  text-align: center;
  th{
    white-space: nowrap;
  }
  tr{
    cursor: pointer;
    white-space: nowrap;
  }
}

.step-box{
  font-size:12px;

  .step{
      background-color: #9d9d9d;
      color:#fff;
      position: relative;
      margin-right: 10px;
      padding:3px 0;
      min-width:60px;
      max-width:70px;
  }
  .active{
      background-color: @cut1;
  }
  .active::after{
      border-left: 10px solid @cut1 !important;
  }
  .bg-warning::after{
    border-left: 10px solid #ffc107 !important;
  }
  .bg-danger::after{
    border-left: 10px solid #dc3545 !important;
  }
  .step1::after{
      content: '';
      width:0; 
      height:0; 
      position: absolute;
      right:-10px;
      top:calc(50% - 10px);
      border-top:10px solid transparent;
      border-bottom: 10px solid transparent;
      border-left: 10px solid #9d9d9d;
  }
  .step2::after{
      content: '';
      width:0; 
      height:0; 
      position: absolute;
      right:-9px;
      top:calc(50% - 10px);
      border-top:10px solid transparent;
      border-bottom: 10px solid transparent;
      border-left: 10px solid #9d9d9d;
  }
  .step2::before{
      content: '';
      width:0; 
      height:0; 
      position: absolute;
      left:0;
      top:calc(50% - 10px);
      border-top:10px solid transparent;
      border-bottom: 10px solid transparent;
      border-left: 10px solid #fff;
  }
  .step3::before{
      content: '';
      width:0; 
      height:0; 
      position: absolute;
      left:0;
      top:calc(50% - 10px);
      border-top:10px solid transparent;
      border-bottom: 10px solid transparent;
      border-left: 10px solid #fff;
  }
}
</style>


